// This file scaffolds our frontend app
// 0. Virtual server build on express just for front end purposes
// 1. Export modules for webpack to transpile
// 2. Routing

// IMPORTS
// REACT LIBRARY
import React from 'react';
import ReactDOM from 'react-dom';

// REACT-BOOTSTRAP ELEMENTS
import NavBarHeader from './NavBarHeader';
import LessonList from './LessonList';

// ROUTING MODULES
import {render} from 'react-dom';
import {BrowserRouter as Router, Route,Link} from 'react-router-dom'


//================================================================
// 0. Virtual server build on express
var cors = require('cors')
var app = express();
app.use(cors())
app.listen(80, function () {
  console.log('CORS-enabled web server listening on port 80')
})
app.get('/products/:id', function (req, res, next) {
  res.json({msg: 'This is CORS-enabled for all origins!'})
})

//================================================================
// 1- Export used modules / components to webpack

const components = { NavBarHeader }
export default components;

//================================================================
// 2- Set up our routing and anchor our app

ReactDOM.render((
   <Router>
      <div>
        <Route exact path="/" component={NavBarHeader} />
        <Route exact path="/abc" component={LessonList} />
      </div>
    </Router>
), document.getElementById( 'app' ))






